import { MutableRefObject, useEffect, useState } from "react";
import { NoInfer } from "utils/types";

/** Type dcrivant les tats d'animation d'un composant et leur transition. */
export type TStateTransitions<StateNames extends string> = {
  [StateName in StateNames]: {
    /** Dure de la transition en millisecondes. Si non spcifie, alors tat infini. */
    duration?: number;
    /** Nom du prochain tat d'animation dfini  la fin de la dure `duration`. */
    toState?: NoInfer<StateNames>;
    /** Gestionnaire appel lorsque cet tat d'animation dmarre. */
    onBegin?: (stateName: StateName) => void;
    /** Gestionnaire appel lorsque cet tat d'animation est termin (uniquement s'il y a une dure `duration`). */
    onEnd?: (stateName: StateName) => void;
  };
};

/**
 * Fonction utilitaire pour crer un objet `TStateTransitions` sans spcifier manuellement le type gnrique `StateNames`,
 * autrement dit l'infrer.
 */
export const asStateTransitions = <StateNames extends string>(obj: TStateTransitions<StateNames>) => obj;

/**
 * Hook permettant de grer facilement des tats d'animation d'un lment d'un composant et leur transition.
 *
 * Permet de dfinir plusieurs tats d'animation, leur dure avant transition  l'tat suivant s'il y en a une,
 * leur tat suivant, ainsi que des gestionnaires appels au dbut et  la fin de chaque tat.
 *
 * L'tat d'animation actuel est affect sur l'attribut `data-state` de l'lment `ref`,
 * pour le slectionner en CSS par exemple.
 *
 * @example
 * Exemple d'utilisation :
 * ```
 * const ref = useRef();
 *
 * const setState = useAnimationState(
 *   ref,
 *   {
 *     opening: {
 *       duration: 250,
 *       toState: "idle",
 *       onBegin: () => console.log("opening begin");
 *     },
 *     closing: {
 *       duration: 250,
 *       onEnd: () => props.close(),
 *     },
 *     idle: {},
 *   },
 *   "opening"
 * );
 *
 * // ...
 * setState("closing");
 * ```
 *
 * @param ref Rfrence de l'lment.
 * @param transitions Objet dcrivant les tats d'animation et les transitions.
 * @param startState Nom de l'tat d'animation de dpart.
 * @returns Fonction pour modifier l'tat d'animation actuel.
 */
function useAnimationState<StateNames extends string>(ref: MutableRefObject<HTMLElement>, transitions: TStateTransitions<StateNames>, startState: NoInfer<StateNames>) {
  /** tat d'animation actuel. */
  const [state, setState] = useState<StateNames>(startState);

  // Affecter le nouvel tat  l'attribut `data-state` de l'lment.
  useEffect(() => {
    if (ref.current) ref.current.dataset.state = state;
  }, [ref, state]);

  // Initialiser le nouvel tat qui vient d'tre affect.
  useEffect(() => {
    const transition = transitions[state];

    transition.onBegin?.(state);

    // Si l'tat  une dure, alors dclencher le timer...
    let timeout: number;

    if (transition.duration) {
      const onStateEnd = () => {
        transition.onEnd?.(state);
        if (transition.toState) setState(transition.toState);
      };

      timeout = window.setTimeout(onStateEnd, transition.duration);
    }

    return () => clearTimeout(timeout);
  }, [ref, transitions, state]);

  return setState;
}

export default useAnimationState;
