import { MutableRefObject, useEffect } from "react";

/**
 * Hook dtectant un clique en dehors d'un lment `ref`.
 * @param ref Rfrence de l'lment en dehors duquel le clique doit tre fait.
 * @param callback Fonction appele lorsqu'un clique est fait en dehors de l'lment `ref`.
 */
function useOutsideClick(ref: MutableRefObject<Node>, callback: (event: Event) => void): void {
  useEffect(() => {
    function listener(e: Event) {
      if (ref.current && !ref.current.contains(e.target as Node)) {
        callback(e);
      }
    }

    document.addEventListener("mousedown", listener);

    return () => {
      document.removeEventListener("mousedown", listener);
    };
  }, [ref, callback]);
}

export default useOutsideClick;
