import { useEffect, useState } from "react";

/**
 * Hook retournant si une requte mdia est vrifie ou non.
 * La requte mdia est revrifie  chaque changement de dimension de la fentre.
 * @param query Requte mdia  vrifier. Par exemple : `(max-width: 768px)`.
 * @returns `true` si la requte mdia `query` est vrifie.
 */
function useMediaQuery(query: string): boolean {
  const [matches, setMatches] = useState(false);

  useEffect(() => {
    const media = window.matchMedia(query);

    if (media.matches !== matches) {
      setMatches(media.matches);
    }

    const listener = () => setMatches(media.matches);

    window.addEventListener("resize", listener);

    return () => window.removeEventListener("resize", listener);
  }, [matches, query]);

  return matches;
}

export default useMediaQuery;
