import { MutableRefObject, useEffect, useMemo, useState } from "react";

/**
 * Hook retournant si un lment `ref` apparat sur le viewport.
 * @param ref Rfrence de l'lment.
 * @param once Faut-il arrter les prochaines dtections lorsque la premire apparition a eu lieu ?
 * @returns `true` si l'lment apparat sur le viewport.
 */
function useInViewport(ref: MutableRefObject<Element>, once: boolean = false): boolean {
  const [isIntersecting, setIsIntersecting] = useState(false);

  const observer = useMemo(
    () =>
      new IntersectionObserver((entries) => {
        setIsIntersecting(entries[0].isIntersecting);
      }),
    []
  );

  // Arrter l'observation s'il y a dj eu une dtection et que `once` est vrai.
  useEffect(() => {
    if (isIntersecting && once) observer.disconnect();
  }, [once, isIntersecting, observer]);

  // Observer lorsque l'lment devient apparat ou disparat du viewport.
  useEffect(() => {
    if (ref.current) observer.observe(ref.current);

    return () => observer.disconnect();
  }, [ref, observer]);

  return isIntersecting;
}

export default useInViewport;
